@import "./vars";

section {
  width: 100%;
  .book-list {
    list-style: none;
    width: 60%;
    border: $background-color solid 0.2rem;
    color: $background-color;
    background-color: $color;
    padding: 1rem;
    border-radius: 1rem;
    cursor: pointer;
    transition: all ease-in-out 0.5s;
    margin: 0 auto;
    margin-bottom: 1rem;
    .book-list__title {
      text-transform: uppercase;
      text-decoration: underline;
      font-weight: bold;
      width: 100%;
      margin-bottom: 0.5rem;
    }
    .book-list__author {
      font-style: italic;
      width: 100%;
    }
    .book-list__publish-year {
      font-size: 0.9rem;
      width: 100%;
    }
    &:hover {
      background-color: $background-color;
      transition: all ease-in-out 0.5s;
      color: $color;
      transform: scale(1.02);
    }
  }
}
