@import "./vars";

.loader {
  position: fixed;
  width: 100%;
  height: 100vh;
  background-color: $color;
  top: 0;
  left: 0;
  z-index: 999;
  .loader__spin {
    width: 10rem;
    height: 10rem;
    position: relative;
    &:before {
      content: "";
      display: block;
      padding-top: 100%;
    }
    &:after {
      content: "";
      position: absolute;
      top: 10rem;
      left: 45%;
      width: 8%;
      height: 8%;
      border-radius: 50%;
      border: 0.5rem solid #ccc;
      border-top-color: $background-color;
      animation: loader 0.9s linear infinite;
    }
  }
}

@keyframes loader {
  to {
    transform: rotate(360deg);
  }
}
